html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}
h2
{
    margin-bottom: 0;
}
hr
{
    margin-top: 20px;
    margin-bottom: 20px;
}
.ms-Button--primary
{
    background-color: #00A171 !important;
}
.ms-Button--primary:hover
{
    background-color: #00F4AC !important;   
}
.ms-TextField.is-active .ms-TextField-fieldGroup 
{
    border-color: #00A171 !important
}
.Application
{ 
    display: -webkit-flex; 
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 5vw;
}
.Application>.Logo
{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    text-decoration: none;
}
.Application>.Logo>.Image
{
    height: 100px;
    width: 100px;
}
.Application>.Logo>.Brand
{
    font-size: 2rem;
    color: #00F4AC;
    text-decoration: none;
    margin-left: 20px;
}
.Application>nav
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 20px;
    width: 30vw;
    background-color: white;
    padding-left: 30px;
    padding-right: 30px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}
.Application>nav>a
{
    color: #999;
    padding: 10px;
    text-decoration: none;
}
.Application>nav>a.active, .Application>nav>a:hover
{
    color: #00F4AC;
    border-bottom: 3px solid #00F4AC;
}
.Application>.Container
{
    width: 30vw;
    padding: 30px;
    margin-top: 10px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    background-color: white;
}

.Dropzone
{
    border: 2px dashed #00F4AC;
    border-radius: 5px;
}
.Dropzone>.Message
{
    cursor: pointer;
    font-weight: 400;
    text-align: center;
    margin: 2em 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.Help .Submit
{
    width: 100%;
}

.About img
{
    border-radius: 50%;
}
.About>img
{
    display: block;
    margin: 0 auto;
}
.About>section 
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

